.footer{
    margin: 200px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
}
.footer-logo{
    display: flex;
    align-items: center;
    gap: 20px;
}
.footer-logo p{
    color: rgb(0, 0, 0);
}
.footer-logo img{
    height: 80px;
}
.footer-icons-container img{
    height: 30px;
    font-size: 46px;
    font-weight:700 ;

}
.footer-links{
    display: flex;
    list-style: none;
    gap: 50px;
    color: grey;
    font-size: 20px;
}
.footer-links li{
    cursor: pointer;
}
.footer-social-icon{
    display: flex;
    gap: 20px;
}
.footer-icons-container{
    
    padding: 10px;
    padding-bottom: 6px;
    background: white;
}
.footer-copyright{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    width: 100%;
    margin-bottom: 30px;
    color: rgb(0, 0, 0);
}
.footer-copyright hr{
    width: 80%;
    border: none;
    border-radius: 10px;
}